}

.temperament {
  align-items: baseline;
  margin-top: 10px;
  display: flex;
}

.temperament-title {
  margin: 0 5px 0 0;
  font-size: 20px;
}

.temperament-description {
  margin: 0;
  font-size: 16px;
}

.error {
  color: red;
  font-size: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  background-color: inherit;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border: 2px solid #fff;
  border-radius: 50%;
  animation: animloader 2s linear 1s infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

